.trending__link-title {
  padding: 10px, 10px;
  margin-bottom: 40px;
  font-size: 28px;
  font-weight: 700;
  line-height: 20px;
  display: block;
  position: relative;
  left: 1.2%;
}

@media screen and (max-width: 360px) {
  .trending__link-title {
    font-size: 25px;
  }
}

.trending__link-title > span {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.trending__link-title:hover > span {
  color: #26fff2;
}

.trending__link-title:hover:after {
  opacity: 1;
  transform: scaleX(1);
}

.trending__link-title:before {
  content: " ";
  width: 8px;
  height: 35px;
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translate(-50%, -50%);
}

.trending__link-title:after {
  content: " ";
  width: 286px;
  height: 3px;
  opacity: 0;
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: -10px;
  left: 0;
  transform: scaleX(0);
}

.see-more {
  background: linear-gradient(317deg, #26fff2 13.2%, #326cff 71.54%);
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.see-more:hover {
  color: #323232;
  transform: scale(1.1);
}

